      align-items: center;
      margin-bottom: 64px;
		}

		// .work-steps__title

		&__title {
      font-weight: 400;
      font-size: 52px;
      color: white;
      margin: 0;
      line-height: 56px;

      span{
        background: linear-gradient(45deg, #fff 0%, #a0b8f7 100%);
