        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
		}

		// .work-steps__text

		&__text {
      font-size: 14px;
      color: white;
      max-width: 356px;
		}

		// .work-steps__application

		&__application {
      display: flex;
      align-items: center;
      gap: 10px;
      padding: 12px 24px;
      border-radius: 24px;
      background-color: var(--accent-color);
      color: white;
      line-height: normal;
		}

		// .work-steps__steps

		&__steps {
      margin-bottom: 64px;
      display: grid;
      grid-template-columns: repeat(6, 1fr);

      svg{
        grid-column: 6 span;
      }
		}

		// .work-steps__info

		&__info {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
		}

		// .work-steps__info-item

		&__info-item {
      display: flex;
      flex-direction: column;
      gap: 12px;
      max-width: 180px;
      &--active{
        padding-left: 12px;


        .work-steps__info-title{
         color: white;
         &::after{
          opacity: 1;
         }
        }
        .work-steps__info-text{
          color: white;
        }
      }
		}

		// .work-steps__info-title

		&__info-title {
      font-weight: 500;
      font-size: 20px;
      color: #474d5a;
      line-height: 1.2;
      position: relative;
      transition: all .2s ease-in;
      &::after{
        display: block;
        content: "";
        position: absolute;
        left: -12px;
        bottom: 0;
        top: 0;
        width: 2px;
        background-color: #2e64e7;
        opacity: 0;
        transition: all .2s ease-in;
      }
		}

		// .work-steps__info-text

		&__info-text {
      font-weight: 400;
      font-size: 14px;
      color: #474d5a;
      line-height: 22px;
      transition: all .2s ease-in;
		}
}
