        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
		}

		// .popular-people__content

		&__content {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 12px;
		}

		// .popular-people__item

		&__item {
      border-radius: 36px;
      position: relative;
      background-color: #000a18;
      overflow: hidden;
      min-height: 400px;
      display: flex;
      flex-direction: column;
      align-items: flex-end;

      &::after{
        display: block;
        content: "";
        position: absolute;
        width: 200px;
        height: 140px;
        border-radius: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        fill: linear-gradient(180deg, #0075ff 0%, #3a60a2 100%);
        filter: blur(60px);
        background-color: #134e9b;
      }
		}

		// .popular-people__item-photo

		&__item-photo {
      position: absolute;
      top: 10px;
      left: 0;
      right: 0;
      z-index: 1;
		}

		// .popular-people__item-info

		&__item-info {
      padding: 24px;
      border-radius: 36px;

      backdrop-filter: blur(120px);
      background: linear-gradient(137deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%);

      display: flex;
      flex-direction: column;

      min-height: 194px;

      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 2;
		}

		// .popular-people__item-name

		&__item-name {
      font-weight: 500;
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 6px;
      color: white;
		}

		// .popular-people__item-activity

		&__item-activity {
      font-weight: 400;
      font-size: 12px;
      line-height: 20px;
      margin-bottom: 12px;
      color:  #7c7c7c;
		}

		// .popular-people__item-text

		&__item-text {
      font-style: italic;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: #fff;
      margin: 0;
		}
}

.application {
    padding: 200px 0;
    margin-bottom: 200px;
    background-image: url(../img/application-bg.jpg);
    background-size: cover;
    border-radius: 64px;
		// .application__content

		&__content {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
		}

		// .application__info

		&__info {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      max-width: 370px;
		}

		// .application__tag

		&__tag {
      font-weight: 400;
      font-size: 18px;

      color: #000;

      padding: 12px 16px;
      background-color: white;
      border-radius: 20px;
      margin-bottom: 32px;
		}

		// .application__title

		&__title {
      font-weight: 400;
      font-size: 52px;

      line-height: 56px;
      color: #fff;
      margin: 0;
      margin-bottom: 32px;
      span{
        background: linear-gradient(45deg, #fff 0%, #2e64e7 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
		}

		// .application__text

		&__text {
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: #fff;
      margin: 0;
		}

		// .application__form

		&__form {
      display: flex;
      flex-direction: column;
      gap: 12px;
      padding: 32px;
      border-radius: 36px;
      backdrop-filter: blur(120px);
      background: linear-gradient(137deg, rgba(255, 255, 255, 0.03) 0%, rgba(255, 255, 255, 0.03) 100%);
      max-width: 660px;
      width: 100%;
		}

		// .application__form-label

		&__form-label {
      display: flex;
      flex-direction: column;
      gap: 6px;
      border-radius: 20px;
      backdrop-filter: blur(120px);
      background: linear-gradient(137deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%);
      padding: 12px;
		}

		// .application__form-label-text

		&__form-label-text {
      font-weight: 400;
      font-size: 12px;
      line-height: 20px;
      color: #7c7c7c;
		}

		// .application__form-input

		&__form-input {
      background-color: transparent;
      font-weight: 400;
      font-size: 18px;

      line-height: 20px;
      color: white;

      &::placeholder{
        color: #7c7c7c;
      }
		}

		// .application__form-submit

		&__form-submit {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 10px;
      padding: 12px;

      font-weight: 400;
      font-size: 16px;
      line-height: normal;
      color: #fff;

      background-color: var(--accent-color);
      border-radius: 24px;
      margin-top: 20px;
		}
}

