html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
  background-color: #000a17;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  color: var(--black-color);
  font-weight: 400;
  line-height: 124%;

  @include fluid-text(17, 16)
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.hidden-mobile{
  @include mobile{
    display: none !important;
  }
}

.hidden-tablet{
  @include tablet{
    display: none !important;
  }
}

.visible-tablet{
  @include for-tablet{
    display: none !important;
  }
}

.visible-mobile{
  @include for-mobile{
    display: none !important;
  }
}
a,
button,
label,
input,
textarea,
select,
svg * {
  transition: all var(--transition-duration) ease-in;
}

input, select{
  outline: transparent;
}

.list{
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 20px;
  &__item{
    padding-left: 22px;
    position: relative;
    font-weight: 600;

    &::after{
      display: block;
      content: "";
      width: 8px;
      height: 8px;
      border-radius: 100%;
      background-color: var(--accent-color);
      position: absolute;
      top: 5px;
      left: 0;
    }
  }
}

.tabs{
  @include tabs
}

.btn-1{
  background-color: var(--accent-color);
  color: var(--light-color);
  padding: 15px 25px;
  border-radius: 10px;
  font-weight: 700;
  transition: all .2s ease-in;
  @include hover{
    box-shadow: 0 4px 9px 0 rgba(0, 0, 0, 0.25);
    background-color: #cf1119;;
  }
}

.btn-2{
  background-color: var(--black-color);
  color: var(--light-color);
  padding: 15px 25px;
  border-radius: 50px;
  font-weight: 700;
  transition: all .2s ease-in;
  display: inline-block;
  @include hover{
    background-color: var(--accent-color);
    color: var(--light-color);
  }
}
