.custom-checkbox {
  position: relative;
}
.custom-checkbox__field {
  appearance: none;
  position: absolute;
}
.custom-checkbox__content {
  padding-left: 35px;
  cursor: pointer;
  font-size: 14px;
}
.custom-checkbox__content a {
  text-decoration: underline;
  color: var(--black-color);
}
@media (any-hover: hover) {
  .custom-checkbox__content a:hover {
    color: var(--accent-color);
  }
}
.custom-checkbox__content--white a {
  color: var(--light-color);
}
.custom-checkbox__content::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  border: 1px solid var(--accent-color);
  width: 23px;
  height: 23px;
  box-sizing: border-box;
  border-radius: 6px;
}
.custom-checkbox__content::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 23px;
  height: 23px;
  box-sizing: border-box;
  background-image: url("../img/accept-arrow.svg");
  background-position: center;
  background-size: 13px 13px;
  background-repeat: no-repeat;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
.custom-checkbox__content {
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;
  color: #7c7c7c;
}
.custom-checkbox__content a {
  text-decoration: underline;
  color: #2e64e7;
}

