.header{
  padding: 42px 0;
  position: absolute;
  top: 0;
  width: 100%;
  left: 0;
  right: 0;
  z-index: 2;
  &__navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
  }
  &__link{
    font-size: 12px;
    color: black;
    display: flex;
    align-items: center;
    gap: 4px;
    padding: 12px 42px;
    background-color: white;
    border-radius: 24px;
    line-height: normal;
  }
}

.logo {
  position: relative;
  z-index: 7;
}

.menu{
  @include tablet{
  position: fixed;
  background-color: white;
  bottom: 0;
  top: 0;
  right: -100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 40px;
  align-items: center;
  transition: right .2s ease-in;
  z-index: 12;
  &--active{
    right: 0;
  }
  }
  &__list {
    display: flex;
    column-gap: 24px;
    @include tablet{
      flex-direction: column;
      row-gap: 20px;
      text-align: center;
      margin: 0 auto
       }
  }
  &__link{
    font-weight: 400;
    font-size: 12px;
    color: white;
    @include hover{
      color: var(--accent-color)
    }
  }

}

.burger{
  @include burger;
  display: none;
  @include tablet{
  display: block;
    z-index: 13;
 }
}
