.footer {
    padding: 32px 0;
    border-radius: 64px 64px 0 0;
    background-color: #0b131c;
		// .footer__content

		&__content {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      gap: 20px;
		}

		// .footer__contacts

		&__contacts {
      display: flex;
      flex-direction: column;
      gap: 32px;
		}

		// .footer__logo-link

		&__logo-link {
		}

		// .footer__logo

		&__logo {
		}

		// .footer__text

		&__text {
      font-weight: 400;
      font-size: 18px;
      line-height: 20px;
      color: white;
		}

		// .footer__social

		&__social {
      display: flex;
      align-items: center;
      gap: 8px;
		}

		// .footer__social-list

		&__social-list {
      display: flex;
      align-items: center;
      gap: 12px;
		}

		// .footer__social-item

		&__social-item {
		}

		// .footer__social-link

		&__social-link {
		}

		// .footer__email

		&__email {
      font-weight: 400;
      font-size: 18px;
      line-height: 20px;
      color: white;
		}

		// .footer__list

		&__list {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 24px;
		}

		// .footer__item

		&__item {
		}

		// .footer__link

		&__link {
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
      color: #fff;
		}

		// .footer__coppyright

		&__coppyright {
      align-self: stretch;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
		}

    &__coppyright-text{
      font-weight: 400;
      font-size: 12px;
      line-height: 20px;
      color: #7c7c7c;
    }

		// .footer__upper

		&__upper {
      text-align: right;
		}
}
